import { AdvancedTableDefaultRow } from "@rp/react/web/common/types"

const descendingComparator = <Item extends AdvancedTableDefaultRow>(
  first: Item,
  second: Item,
  orderBy: string,
) => {
  const firstString = (first[orderBy]?.toString() || "").toLowerCase()
  const secondString = (second[orderBy]?.toString() || "").toLowerCase()

  return secondString.localeCompare(firstString, undefined, { numeric: true, sensitivity: "base" })
}

const getComparator = <Item extends AdvancedTableDefaultRow>(
  order: "asc" | "desc",
  orderBy: string,
) => {
  return order === "desc"
    ? (first: Item, second: Item) => descendingComparator<Item>(first, second, orderBy)
    : (first: Item, second: Item) => -descendingComparator<Item>(first, second, orderBy)
}

const stableSort = <Item extends AdvancedTableDefaultRow>(
  array: Array<Item> = [],
  comparator: (first: Item, second: Item) => number,
) => {
  const stabilizedThis = array.map((element, index) => ({ element, index }))
  stabilizedThis.sort((a, b) => {
    const order = comparator(a.element, b.element)
    if (order !== 0) return order
    return a.index - b.index
  })
  return stabilizedThis.map(el => el.element)
}

export { getComparator, stableSort, descendingComparator }
